import React, { Component, Suspense } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import UrlInput from '../../../CommonComponents/UrlInput';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import PreviewOnsite from './PreviewOnsite';
import ColorPicker from '../../../CommonComponents/ColorPicker';
import WidgetOnsiteButton from '../../../Templates/ThemeTemplates/Components/WidgetOnsiteButton';

class WebsiteOnsite extends Component {
    state = { error: false, buttonText: "Scan QR or Tap Here to Upload UGC", bgColor: "#000", fcColor: "#000", fontSize: 8 };
    onBGColorChange = (value) => {
        this.setState({ bgColor: value })

    }
    onFCColorChange = (value) => {
        this.setState({ fcColor: value })
    }
    onFontSize = event => {
        this.setState({ fontSize: event.target.value })
    }
    onChangeText = event => {
        this.setState({ buttonText: event.target.value })
    }
    render() {
        const { error, buttonText, bgColor, fcColor, fontSize } = this.state
        const { tinyUrl, onClickToOpen, uniqueCode } = this.props
        return <div className="d-flex align-items-start flex-column flex-md-row">
            <div className="uploadInnerAside flex-shrink-0 mb-3 mb-sm-0">
                <PerfectScrollbar>
                    <div className="upload_aside_scroll">
                        <div className="mb-2">
                            <label className="mb-1 fs-9 fw-medium">Button Text</label>
                            <div className="">
                                <input type="text" name="title" className={`form-control form-control-sm ${error ? 'is-invalid' : ''}`} placeholder="Enter Button Title..." value={buttonText} onChange={this.onChangeText} />
                                {error ? <span className="invalid-feedback">This field is required</span> : null}
                            </div>
                        </div>
                        <label className="d-flex align-items-center justify-content-between h-40px py-1 position-relative">
                            <span className="ms-0 fs-9 fw-medium">Background Color</span>
                            <Suspense fallback={<></>}>
                                <ColorPicker color={bgColor} onChangeColor={this.onBGColorChange} />
                            </Suspense>
                        </label>
                        <label className="d-flex align-items-center justify-content-between h-40px py-1 position-relative">
                            <span className="ms-0 fs-9 fw-medium">Font Color</span>
                            <Suspense fallback={<></>}>
                                <ColorPicker color={fcColor} onChangeColor={this.onFCColorChange} />
                            </Suspense>
                        </label>
                        <div className="d-flex align-items-stretch flex-column justify-content-between py-1">
                            <div className="">
                                <p className="my-2"><span className="fs-9 fw-medium">Font Size</span></p>
                            </div>
                            <div className="d-flex align-items-center w-100 mb-1">
                                <div className="d-flex flex-grow-1 pe-3">
                                    <div className="form-group pt-1 w-100">
                                        <input type="range" className="form-range" min="10" max="30" step="1" value={fontSize} defaultValue={fontSize} id="formControlRange" onChange={this.onFontSize} />
                                    </div>
                                </div>
                                <div className="d-flex align-items-center">
                                    <div className="form-group me-1">
                                        <input type="number" className="form-control form-control-sm w-54px" value={fontSize} defaultValue={fontSize} onChange={this.onFontSize} />
                                    </div>
                                    px
                                </div>
                            </div>
                        </div>
                    </div>
                </PerfectScrollbar>

            </div>
            <div className="d-flex flex-grow-1 w-100 flex-column">
                <UrlInput inputUrl={tinyUrl} inputId={'onsite1'} inputSize={''} onClickToOpen={onClickToOpen}
                    share={[{ status: true, tooltip: 'Share Onsite Url' }]}
                    copy={[{ status: true, tooltip: 'Share Onsite Url' }]}
                    edit={[{ status: false, tooltip: 'Share Onsite Url' }]} />
                {/* <WidgetOnsiteButton templateFormData={''} /> */}

                <code>
                    &lt;style&gt;

                    &lt;/style&gt;
                    &lt;div class="tb_onsite_upload_btn_wrap"&gt;
                    &lt;div class="tb_onsite_upload_btn"&gt;
                    &lt;div style="backgroundColor: {bgColor}" class='tb_onsite_btn'&gt;
                    &lt;div class="tb_onsite_qr_btn_wrap"&gt;
                    &lt;div class='tb_onsite_qr_code'&gt;
                    &lt;img src={uniqueCode} height={60} width={60} /&gt;
                    &lt;/div&gt;
                    &lt;div class="tb_onsite_btn_txt" style="color: {fcColor};font-size: {fontSize}px;text-align:center;"&gt;
                    {buttonText}
                    &lt;/div&gt;
                    &lt;/div&gt;
                    &lt;/div&gt;
                    &lt;/div&gt;
                    &lt;/div&gt;
                </code>
            </div>
        </div>
    }
}

export default WebsiteOnsite;